import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step16(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [previousmarriage, setpreviousmarriage] = useState({
    nameOfYourPriorSpouse: "",
    whenYouMarried: "",
    whereYouMarried: "",
    whenYourMarriageEnded: "",
    howYourMarriageEnded: "",

    nameOfSpousePriorSpouse: "",
    whenSpouseMarried: "",
    whereSpouseMarried: "",
    whenSpouseMarriageEnded: "",
    howSpouseMarriageEnded: "",
  });

  const formRef = new React.createRef();
     const validate = () => {
       setTouched(true);
       const isValid = formRef.current.checkValidity();
       setErrorMsg(!isValid ? "Please enter date" : "");
     };

    
  const handleInputChange = (name,value) => {
    const values = previousmarriage;
      values[name] =   value
    setpreviousmarriage(values)
    onChangeField("e", "priorMarriage",previousmarriage);
  }
  
  return (
    <Step heading={`Have you been married more than once?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "marriedMoreThanOnce", true);
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "marriedMoreThanOnce", false);
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
            {state.marriedMoreThanOnce && (
              <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                <MDBCard className="mt-5 p-4">
                  <MDBRow>
                    <h5>Your Prior marriage details</h5>
                  </MDBRow>
                  <MDBRow>
                    <MDBCol md={12}>
                      <MDBInput
                        color="pink"
                        label="Name of prior Spouse"
                        group
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        type="text"
                        name="nameOfYourPriorSpouse"
                        value={previousmarriage.nameOfYourPriorSpouse}
                        onChange={(e) => {
                          validate();
                          handleInputChange(e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow center className="mt-4">
                    <MDBCol>
                      <MDBInput
                        label="marriage on"
                        group
                        type="date"
                        name="whenYouMarried"
                        value={previousmarriage.whenYouMarried}
                        onChange={(event) =>
                          handleInputChange(
                            event.target.name,
                            event.target.value
                          )
                        }
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Place"
                        group
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        type="text"
                        name="whereYouMarried"
                        value={previousmarriage.whereYouMarried}
                        onChange={(e) => {
                          validate();
                          handleInputChange(e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow center className="mt-4">
                    <MDBCol>
                      <MDBInput
                        label="marriage ended on"
                        group
                        type="date"
                        name="whenYourMarriageEnded"
                        value={previousmarriage.whenYourMarriageEnded}
                        onChange={(event) =>
                          handleInputChange(
                            event.target.name,
                            event.target.value
                          )
                        }
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Reason"
                        group
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        type="text"
                        name="howYourMarriageEnded"
                        value={previousmarriage.howYourMarriageEnded}
                        onChange={(e) => {
                          validate();
                          handleInputChange(e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                </MDBCard>
                <MDBCard className="mt-5 p-4">
                  <MDBRow>
                    <h5>Spouse Prior marriage details</h5>
                  </MDBRow>
                  <MDBRow>
                    <MDBCol md={12}>
                      <MDBInput
                        color="pink"
                        label="Name of prior Spouse"
                        group
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        type="text"
                        name="nameOfSpousePriorSpouse"
                        value={previousmarriage.nameOfSpousePriorSpouse}
                        onChange={(e) => {
                          handleInputChange(e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow center className="mt-4">
                    <MDBCol>
                      <MDBInput
                        label="marriage on"
                        group
                        type="date"
                        name="whenSpouseMarried"
                        value={previousmarriage.whenSpouseMarried}
                        onChange={(event) =>
                          handleInputChange(
                            event.target.name,
                            event.target.value
                          )
                        }
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Place"
                        group
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        type="text"
                        name="whereSpouseMarried"
                        value={previousmarriage.whereSpouseMarried}
                        onChange={(e) => {
                  
                          handleInputChange(e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow center className="mt-4">
                    <MDBCol>
                      <MDBInput
                        label="marriage ended on"
                        group
                        type="date"
                        name="whenSpouseMarriageEnded"
                        value={previousmarriage.whenSpouseMarriageEnded}
                        onChange={(event) =>
                          handleInputChange(
                            event.target.name,
                            event.target.value
                          )
                        }
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                    <MDBCol>
                      <MDBInput
                        color="pink"
                        label="Reason"
                        group
                        pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                        type="text"
                        name="howSpouseMarriageEnded"
                        value={previousmarriage.howSpouseMarriageEnded}
                        onChange={(e) => {
                    
                          handleInputChange(e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0"
                      />
                    </MDBCol>
                  </MDBRow>
                </MDBCard>
                <MDBRow
                  center
                  className="d-flex flex-column align-items-center"
                >
                  {true  && (
                    <div className="text-danger mb-2">{errorMsg}</div>
                  )}
                  { true && (
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  )}
                  
                </MDBRow>
              </form>
            )}
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
