//Have you ever registered under selective service? 
//If answered yes, " What is the selective service number? What was the date registered? 
//If answered No, "Were you required to do so? yes/no 



import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
    const [errorMsg, setErrorMsg] = useState("");
    const [selectiveService, setSelectiveService] = useState({
        Registered: null,
        selectiveServiceNumber: "",
        dateRegistered: "",
        WereyouRequiredTo: "",
        WhyDidntRegister:""
        
    });
  
  useEffect(() => {
    
    validate()
  })

  const formRef = new React.createRef();

  const validate = () => {
      setTouched(true);

      if(formRef.current){
         const isValid = formRef.current.checkValidity();
            setErrorMsg(!isValid ? "Please enter valid details" : "");
      }
  
  };

  const handleChange = (name, value) => {
    const values = selectiveService;
    values[name] = value;
    setSelectiveService(values);
      onChangeField("e", "registeredSelectiveService", selectiveService);
      validate()
  };

  return (
    <Step heading={`Have you ever registered under selective service? `}>
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    handleChange("Registered", true);
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    handleChange("Registered", false);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
            {selectiveService.Registered && (
              <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                <MDBRow>
                  <MDBCol md={12}>
                    <MDBInput
                      color="pink"
                      label="Selective Service Number"
                      group
                      type="number"
                      name="selectiveServiceNumber"
                      value={selectiveService.selectiveServiceNumber}
                      onChange={(e) => {
                        handleChange("selectiveServiceNumber", e.target.value);
                      }}
                      required
                      containerClass="mb-0 mt-5"
                    />
                    <div class="invalid-feedback">
                      Please provide a valid Number.
                    </div>
                  </MDBCol>
                </MDBRow>
                <MDBRow center>
                  <MDBCol>
                    <MDBInput
                      label=" Date"
                      group
                      type="date"
                      name="dateRegistered"
                      value={selectiveService.dateRegistered}
                      onChange={(e) => {
                        handleChange(e.target.name, e.target.value);
                      }}
                      required
                      containerClass="mb-0"
                    />
                  </MDBCol>
                </MDBRow>
                <MDBRow
                  center
                  className="d-flex flex-column align-items-center"
                >
                  {touched && errorMsg.length > 0 && (
                    <div className="text-danger mb-2">{errorMsg}</div>
                  )}
                  {touched && !errorMsg && (
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn
                      color={"grey"}
                      disabled
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  )}
                </MDBRow>
              </form>
            )}

            {selectiveService.Registered == false && (
              <MDBRow center className="mt-4">
                <MDBCol md={12}>
                  <h3 style={{ display: "block", textAlign: "center" }}>
                    Were You required to Register?{" "}
                  </h3>
                </MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      handleChange("WereyouRequiredTo", true);
                    }}
                  >
                    Yes
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      handleChange("WereyouRequiredTo", false);
                      history.push(nextStep);
                    }}
                  >
                    No
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
            )}

            {selectiveService.WereyouRequiredTo && (
              <>
                <MDBRow>
                  <MDBCol md={12}>
                    <MDBInput
                      color="pink"
                      label="Why didn't you Register ?"
                      group
                      pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                      type="text"
                      name="WhyDidntRegister"
                      value={selectiveService.WhyDidntRegister}
                      onChange={(e) => {
                        handleChange("WhyDidntRegister", e.target.value);
                      }}
                      required
                      containerClass="mb-0 mt-5"
                    />
                  </MDBCol>
                </MDBRow>
                <MDBRow
                  center
                  className="d-flex flex-column align-items-center"
                >
                  {touched && errorMsg.length > 0 && (
                    <div className="text-danger mb-2">{errorMsg}</div>
                  )}
                  {touched && !errorMsg && (
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn
                      color={"grey"}
                      disabled
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  )}
                </MDBRow>
              </>
            )}
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
