// add conditionals --shubs

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";


export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [count, setcount] = useState(1)
  const formRef = new React.createRef();

  const [otherFactors, setOtherFactors] = useState(state.otherFactorsCntd);

  const Questions = [
    {
      question: "Since becoming a permanent resident, have you ",
      name: "IncomeTaxReturn",
      options: [
        {
          opt: "failed to file a federal income tax return?",
          name: "failedToFileFederalIncomeTaxReturn",
        },
        {
          opt:
            "failed to file a tax return because you considered yourself a nonresident?",
          name: "failedToFileAtaxReturn",
        },
        {
          opt: "filed a tax return as a nonresident?",
          name: "filedTaxReturn",
        },
      ],
    },
    {
      question:
        "Have you ever practiced, used, or been involved in any of the following:",
      name: "IncomeTaxReturn",
      options: [
        {
          opt: "alcohol abuse/alcoholism?",
          name: "alcoholism",
        },
        {
          opt: "polygamy?",
          name: "polygamy",
        },
        {
          opt: "prostitution?",
          name: "prostitution",
        },
        {
          opt: "illegal entry of foreigners ??",
          name: "illegalEntry",
        },
        {
          opt: "drugs (narcotics)?",
          name: "drugs",
        },
        {
          opt: "illegal gambling ?",
          name: "gambling",
        },
      ],
    },
  ];

  const validate = () => {
    setTouched(true);
    
    const isValid = formRef.current.checkValidity();
    setErrorMsg(count<9 ? "Please answer all the questions" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
    validate()
  }, []);

  const handleChange = (qname, oname,value,qindex) => {
      const values = otherFactors;
    validate()
    setcount(count+1)
      values[qindex]["opts"][oname] = value
    setOtherFactors(values);
    onChangeField("e", "otherFactorsCntd", otherFactors);
  };

  return (
    <Step heading={"Other Factors Continued"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form
          className="was-validated"
          noValidate
          onSubmit={(e) => e.preventDefault()}
          ref={formRef}
        >
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, qindex) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontWeight: "bold",
                          fontSize: "larger",
                        }}
                      >
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>

                  {q.options.map((o, index) => (
                    <MDBRow className="align-items-center mt-3">
                      <MDBCol md={8}>
                        <div>
                          <h6 style={{ width: "100%" }}>
                            {index + 1}. {o.opt}
                          </h6>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <MDBFormInline className="justify-content-center">
                          <div class="form-check mr-3">
                            <input
                              type="radio"
                              class="form-check-input"
                              name={`${index}`}
                              onClick={() =>
                                handleChange(q.name, o.name, true, qindex)
                              }
                              checked={
                                otherFactors[qindex]["opts"][o.name] === true
                                  ? true
                                  : false
                              }
                              label="Yes"
                              type="radio"
                              id="radio1"
                              style={{ display: "flex" }}
                              containerClass="mr-5"
                              required
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              Yes
                            </label>
                          </div>
                          <div class="form-check mr-3">
                            <input
                              type="radio"
                              class="form-check-input"
                              name={`${index}`}
                              onClick={() =>
                                handleChange(q.name, o.name, false, qindex)
                              }
                              checked={
                                otherFactors[qindex]["opts"][o.name] === false
                                  ? true
                                  : false
                              }
                              label="Yes"
                              type="radio"
                              id="radio1"
                              style={{ display: "flex" }}
                              containerClass="mr-5"
                              required
                            />
                            <label
                              class="form-check-label"
                              for="materialUnchecked"
                            >
                              No
                            </label>
                          </div>
                         
                        </MDBFormInline>
                      </MDBCol>
                    </MDBRow>
                  ))}
                </MDBCard>
              ))}

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
