// Full name of child? Date of birth? Birth Country? Citizenship? Alien #? Current Adress?
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import { countries } from "./countries";
export default function Step1(props) {
  const blankChild = {
    fullName: "",
    inde: "",
    DOB: "",
    birthCountry: "",
    childrenCitizenship: "",
    alienNumber: "",
    currentAddress: "",
  };
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [childrenDetails, setChildrenDetails] = useState([]);
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "" : "Please fill out proper values");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const loop = (n) => {
    return new Promise(async (resolve) => {
      var childs = [];
      for (var i = 0; i < n; i++) {
        
        console.log(i);
        childs.push(blankChild);
        if (i == 0) {
          childs[0].inde = 1
        } else {
          childs[1].inde = 2
        }
        
        console.log(childs[i].inde + " this is indez")
        console.log("running loop");
      }
      console.log(childs);
      return resolve(childs);
    });
  };

  const handleInputChange = (index,name, value) => {
    const values = [...childrenDetails];
    if (name === "fullName") {
      console.log(index);
      console.log(values[index])
      values[index].fullName = value;
            console.log("this is state" + JSON.stringify(childrenDetails));

    } else if (name === "DOB") {
      values[index].DOB = value;
    } else if (name === "birthCountry") {
      values[index].birthCountry = value;
    } else if (name === "childrenCitizenship") {
      values[index].childrenCitizenship = value;
    } else if (name === "alienNumber") {
      values[index].alienNumber = value;
    } else if (name === "currentAddress") {
      values[index].currentAddress = value;
    }

    // values[e.target.dataset.idx][e.target.name] = e.target.value;
    
    // console.log(index);

    validate();
    setChildrenDetails(values);
    onChangeField("e", "childrenDetails", childrenDetails);
    console.log(values)
  };

  const addChildren = async (n) => {
    const values = [...childrenDetails];
    values.push(blankChild)
    setChildrenDetails(values)
  };

  
  const removeChildren = (index) => {
    const values = [...childrenDetails];
    values.splice(index, 1);
    setChildrenDetails(values);
  };
  return (
    <Step
      heading={
        "How many children do you have? Please complete the following information for each child"
      }
    >
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {childrenDetails.map((child, index, array) => {
                return (
                  <MDBCard className="mt-3" key={`${child}-${index}`}>
                    <div className="p-3">
                      <MDBRow>
                        <MDBCol md={12}>
                          <MDBInput
                            color="pink"
                            label="Full Name"
                            group
                            pattern="\d[\d,]*"
                            type="text"
                            data-idx={index}
                            name="fullName"
                            value={JSON.stringify(array)[index].fullName}
                            onChange={(event) => {
                                validate();
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              );
                            }}
                            // onChange={(e)=>handleInputChange(e)}
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow center>
                        <MDBCol className="mt-3">
                          <MDBInput
                            label=" Date of Birth"
                            group
                            type="date"
                            name="DOB"
                            value={child.DOB}
                            onChange={(event) => {
                              validate();
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              );
                            }}
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow center className="">
                        <MDBCol>
                          <MDBInput
                            color="pink"
                            label="Alien Number"
                            group
                            
                            type="number"
                            name="alienNumber"
                            value={child.alienNumber}
                            onChange={(event) => {
                              validate();
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              );
                            }}
                            required
                            containerClass="mb-0 mt-3"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow>
                        <MDBCol md={12}>
                          <MDBInput
                            color="pink"
                            label="current Address"
                            group
                            pattern="\d[\d,]*"
                            type="text"
                            name="currentAddress"
                            value={child.currentAddress}
                            onChange={(event) => {
                              validate();
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              );
                            }}
                            required
                            containerClass="mb-0 mt-3"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow center>
                        <MDBCol className="mt-3">
                          <label>Country of Birth</label>
                          <select
                            className="browser-default custom-select"
                            name="birthCountry"
                            value={child.birthCountry}
                            onChange={(event) => {
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              );
                            }}
                            required
                          >
                            <option selected disabled>
                              Choose your option
                            </option>
                            {countries.map((country) => (
                              <option value={country.name}>
                                {country.name}
                              </option>
                            ))}
                          </select>
                        </MDBCol>
                      </MDBRow>
                      <MDBRow center>
                        <MDBCol className="mt-3">
                          <label>Citizenship</label>
                          <MDBInput
                            type="text"
                            name="childrenCitizenship"
                            value={child.childrenCitizenship}
                            label="Citizenship"
                            onChange={(event) => {
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              );
                            }}
                            required
                            containerClass="mt-0 pt-0"
                          />
                        </MDBCol>
                      </MDBRow>
                    </div>
                  </MDBCard>
                );
              })}
              <MDBRow className="d-flex  justify-items-center mt-5">
                <MDBCol className="d-flex justify-content-center">
                  <MDBBtn label="+" rounded onClick={() => addChildren()}>
                    +
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-flex justify-content-center">
                  <MDBBtn label="+" onClick={() => removeChildren()}>
                    -
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
