import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {countries} from "./countries"

export default function Step10(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please enter valid details" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step heading={`What date and place were you and your spouse married in?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol>
                  <MDBInput
                    label="Date of Marriage"
                    group
                    type="date"
                    name="dateOfMarriage"
                    
                    value={state.dateOfMarriage}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0 mt-5"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center>
                <MDBCol>
                  <label className="mt-3">Place of Marriage</label>
                  <select
                    className="browser-default custom-select mb-3 "
                    name="placeOfMarriage"
                    containerClass="mb-0 mt-5"
                    value={state.placeOfMarriage}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>
                    {countries.map((country) => (
                      <option value={country.name}>{country.name}</option>
                    ))}
                  </select>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
const normalizeInput = (value, previousValue) => {
  if (!value) return value;
  const currentValue = value.replace(/[^\d]/g, "");
  if (!previousValue || value.length > previousValue.length) {
    if (currentValue.length <= 3) return currentValue;
    if (currentValue.length === 3) return `(${currentValue})`;
    if (currentValue.length <= 6)
      return `(${currentValue.slice(0, 3)}) ${currentValue.slice(3)}`;
    if (currentValue.length === 6)
      return `(${currentValue.slice(0, 3)}) ${currentValue.slice(3)}-`;
    return `(${currentValue.slice(0, 3)}) ${currentValue.slice(
      3,
      6
    )}-${currentValue.slice(6, 10)}`;
  }
};
