import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step1(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [parentInfo, setParentInfo] = useState({
    parentsUSCitizens: "",
    address: "",
    Relationship: "",
    parentwas: "",
    nameOfParent: "",
    dateOflegitimization: "",
    legalCustody: "",
  });
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out proper values" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const handleChange = (name, value) => {
    const values = parentInfo;
    values[name] = value;
    setParentInfo(values);
    onChangeField("e", "parentalInfo", parentInfo);
  };

  return (
    <Step
      heading={
        "If you are a permant resident child of U.S. citizen parent(s), please complete the following"
      }
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={12} md={8} className="mt-4 align-items-center">
              <MDBRow center className="align-items-center">
                <MDBCol md={8} className=" align-content-center">
                  <div>
                    <h6 style={{ fontWeight: "bold" }}>
                      How many of your parents are U.S. citizens?
                    </h6>
                  </div>
                </MDBCol>
                <MDBCol md={4}>
                  <MDBFormInline className="justify-content-center">
                    <div class="form-check mr-3">
                      <input
                        type="radio"
                        class="form-check-input"
                        name="check"
                        onClick={(e) =>
                          handleChange("parentsUSCitizens", "one")
                        }
                        checked={
                          parentInfo["parentsUSCitizens"] === "one"
                            ? true
                            : false
                        }
                        label="Yes"
                        type="radio"
                        id="radio1"
                        style={{ display: "flex" }}
                        containerClass="mr-5"
                        required
                      />
                      <label class="form-check-label" for="materialUnchecked">
                        One
                      </label>
                    </div>
                    <div class="form-check mr-3">
                      <input
                        type="radio"
                        class="form-check-input"
                        name="check"
                        onClick={() =>
                          handleChange("parentsUSCitizens", "Both")
                        }
                        checked={
                          parentInfo["parentsUSCitizens"] === "Both"
                            ? true
                            : false
                        }
                        label="Yes"
                        type="radio"
                        id="radio1"
                        style={{ display: "flex" }}
                        containerClass="mr-5"
                        required
                      />
                      <label class="form-check-label" for="materialUnchecked">
                        Both
                      </label>
                    </div>
                  </MDBFormInline>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md={12}>
                  <MDBInput
                    color="pink"
                    label="Complete name of one U.S. citizen parent? "
                    group
                    pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                    type="text"
                    name="nameOfParent"
                    value={parentInfo["nameOfParent"]}
                    onChange={(e) => {
                      validate();
                      handleChange(e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0 mt-5"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md={12}>
                  <MDBInput
                    color="pink"
                    label="Address of the parent "
                    group
                    type="text"
                    name="address"
                    value={parentInfo["address"]}
                    onChange={(e) => {
                      validate();
                      handleChange(e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
                <MDBRow>
                  <MDBCol sm={12} md={12} className="mt-3">
                    <div>
                      <h6 style={{ fontWeight: "bold" }}>
                        Relationship to you:
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol sm={12}>
                    <MDBFormInline>
                      <MDBCol>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="check1"
                            onClick={(e) =>
                              handleChange("Relationship", "Natural Parent")
                            }
                            checked={
                              parentInfo["Relationship"] === "Natural Parent"
                                ? true
                                : false
                            }
                            type="radio"
                            style={{ display: "flex" }}
                            containerClass="mr-5"
                            required
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Natural Parent
                          </label>
                        </div>
                      </MDBCol>
                      <MDBCol>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="check1"
                            onClick={() =>
                              handleChange("Relationship", "Adoptive Parent")
                            }
                            checked={
                              parentInfo["Relationship"] === "Adoptive Parent"
                                ? true
                                : false
                            }
                            type="radio"
                            style={{ display: "flex" }}
                            containerClass="mr-5"
                            required
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Adoptive Parent
                          </label>
                        </div>
                      </MDBCol>

                      <MDBCol>
                        <div class="form-check mr-3">
                          <input
                            type="radio"
                            class="form-check-input"
                            name="check1"
                            onClick={() =>
                              handleChange(
                                "Relationship",
                                "Parent of child legitimized after birth"
                              )
                            }
                            checked={
                              parentInfo["Relationship"] ===
                              "Parent of child legitimized after birth"
                                ? true
                                : false
                            }
                            type="radio"
                            style={{ display: "flex" }}
                            containerClass="mr-5"
                            required
                          />
                          <label
                            class="form-check-label"
                            for="materialUnchecked"
                          >
                            Parent of child legitimized after birth
                          </label>
                        </div>
                      </MDBCol>
                    </MDBFormInline>
                  </MDBCol>
                </MDBRow>
                <MDBRow className="align-items-center">
                  <MDBCol md={8} className="mt-3">
                    <div>
                      <h6 style={{ fontWeight: "bold" }}>
                        Does this parent have legal custody of you
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <div class="form-check ml-2 form-check-inline">
                        <input
                          onClick={(e) => handleChange("legalCustody", "Yes")}
                          checked={
                            parentInfo["legalCustody"] === "Yes" ? true : false
                          }
                          label="Yes"
                          type="radio"
                          id="radio1"
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="defaultCheck1"
                        />
                        <label class="form-check-label" for="defaultCheck1">
                          Yes
                        </label>
                      </div>
                      <div class="form-check ml-2 form-check-inline">
                        <input
                          onClick={(e) => handleChange("legalCustody", "no")}
                          checked={
                            parentInfo["legalCustody"] === "no" ? true : false
                          }
                          label="No"
                          type="radio"
                          id="radio1"
                          class="form-check-input"
                          type="checkbox"
                          value=""
                          id="defaultCheck1"
                        />
                        <label class="form-check-label" for="defaultCheck1">
                          No
                        </label>
                      </div>
                    </MDBFormInline>
                  </MDBCol>
                </MDBRow>
                {parentInfo["Relationship"] ===
                  "Parent of child legitimized after birth" && (
                  <MDBCol md={12} className="mt-3">
                    <MDBInput
                      color="pink"
                      label="Date of legitimization "
                      group
                      type="date"
                      name="dateoLegalizationOrAdoption"
                      value={parentInfo.dateOflegitimization}
                      onChange={(e) => {
                        validate();
                        handleChange("dateOflegitimization", e.target.value);
                      }}
                      required
                      style={{ position: "static" }}
                      containerClass="mb-0 ml-0"
                    />
                  </MDBCol>
                )}
                {parentInfo["Relationship"] === "Adoptive Parent" && (
                  <MDBCol md={12} className="mt-3">
                    <MDBInput
                      color="pink"
                      label="Date of legitimization "
                      group
                      type="date"
                      name="dateoLegalizationOrAdoption"
                      value={parentInfo.dateOflegitimization}
                      onChange={(e) => {
                        validate();
                        handleChange("dateOflegitimization", e.target.value);
                      }}
                      required
                      containerClass="mb-0 "
                    />
                  </MDBCol>
                )}
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
