// Employers Name , Employers work address, Current Occupation, dates work at job
//Please list all of your employment history during the last 5 years? Start with your most recent job?

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import MapComponent from './Mapcomponent'
export default function Step1(props) {
  const blankEmployer = {
    name: "",
    workAddress: "",
    occupation: "",
    fromDate: "",
    toDate: "",
  };
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [employer, setEmployer] = useState(state.employer);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out proper values" : "");
  };
  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const handleInputChange = (index, name, value) => {
    const values = [...employer]
    if (name === "name") {
      values[index].name = value;
      console.log("gotttttt")
    }
    else if (name === "workAddress") {
      values[index].workAddress = value;
    }
    else if (name === "occupation") {
      values[index].occupation = value;
    }
    else if (name === "fromDate") {
      values[index].fromDate = value;
    }
    else if (name === "toDate") {
      values[index].toDate = value;
    }
    console.log(index)
    
    validate()
    setEmployer(values);
    onChangeField("e", "employer", employer);
  }

  const addEmployer = () => {
    const values = [...employer]
    validate();

      values.push(blankEmployer);
      setEmployer(values);
    
  };

  const removeEmployer = (index) => {
    const values = [...employer]
    if(employer.length>1)
    values.splice(index, 1)
    setEmployer(values)
  }
  
  return (
    <Step heading={"Your last 5 year employment details"}>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={12} md={8}>
              {employer.map((e, index) => {
                return (
                  <MDBCard style={{ marginTop: "10px" }} key={`${e}-${index}`}>
                    <div style={{ padding: "10px" }}>
                      <MDBRow>
                        <MDBCol md={12}>
                          <MDBInput
                            color="pink"
                            label="Employers Name"
                            group
                            pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                            type="text"
                            name="name"
                            value={e.name}
                            onChange={(event) =>
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              )
                            }
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow>
                        <MDBCol md={12}>
                          <MDBInput
                            color="pink"
                            label=" Address, street, city ,country"
                            group
                            type="text"
                            name="workAddress"
                            value={e.workAddress}
                            onChange={(event) =>
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              )
                            }
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow>
                        <MDBCol md={12}>
                          <MDBInput
                            color="pink"
                            label=" Occupation"
                            group
                            type="text"
                            name="occupation"
                            value={e.occupation}
                            onChange={(event) =>
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              )
                            }
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow center className="mt-4">
                        <MDBCol>
                          <MDBInput
                            label="Worked from "
                            group
                            type="date"
                            name="fromDate"
                            value={e.fromDate}
                            onChange={(event) =>
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              )
                            }
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                        <MDBCol>
                          <MDBInput
                            label=" to"
                            group
                            type="date"
                            name="toDate"
                            value={e.toDate}
                            onChange={(event) =>
                              handleInputChange(
                                index,
                                event.target.name,
                                event.target.value
                              )
                            }
                            required
                            containerClass="mb-0"
                          />
                        </MDBCol>
                      </MDBRow>
                    </div>
                  </MDBCard>
                );
              })}
              <MDBRow className="d-flex  justify-items-center mt-5">
                <MDBCol className="d-flex justify-content-center">
                  {touched && !errorMsg && (
                    <MDBBtn label="+" rounded onClick={() => addEmployer()}>
                      +
                    </MDBBtn>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn label="+" rounded color={"grey"} disabled onClick={() => addEmployer()}>
                      +
                    </MDBBtn>
                  )}
                </MDBCol>
                <MDBCol className="d-flex justify-content-center">
                  <MDBBtn label="+" onClick={() => removeEmployer()}>
                    -
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
 