// confused about the question -- shubs

import React ,{ useState} from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
  MDBCard,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step20(props) {
  const blankOrganization = {
    nameAndLocation: "",
    DateFrom: "",
    DateTo: "",
    natureOfOrganization: "",
  }
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const formRef = new React.createRef();
  const [touched, setTouched] = useState(false)
  const [errorMsg, setErrorMsg] = useState("");
  const [organization, setOrganization] = useState([blankOrganization])
   const validate = () => {
     setTouched(true);
     const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please enter date" : "");
  };


  
  const handleChange = (index,name, value) => {
    const values = organization;
    console.log(values[1]);
    values[index][name] = value;
    setOrganization(values);
    onChangeField("e", "organization", organization);
  };
  
  
   const addOrganization = () => {
     const values = [...organization];
     validate();
     values.push(blankOrganization);
     setOrganization(values);
   };

   const removeOrganization = (index) => {
     const values = [...organization];
    values.splice(index, 1);
     console.log(index);
     setOrganization(values);
   };

  return (
    <Step
      heading={`Are you currently a member of any organization, club or group (including military service)? `}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "memberOfOrganization", true);
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "memberOfOrganization", false);
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>

              {/* Name and location of the organization, dates from to, nature of organization */}
              <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                {state.memberOfOrganization && (
                  <>
                    <MDBRow center>
                      {organization.map((o, index) => (
                        <MDBCard className="mt-5">
                          <div className="p-4">
                            <MDBRow>
                              <MDBCol>
                                <MDBInput
                                  label="Name and location "
                                  group
                                  type="text"
                                  name="nameAndLocation"
                                  value={o.nameAndLocation}
                                  onChange={(e) => {
                                    validate();
                                    handleChange(
                                      index,
                                      e.target.name,
                                      e.target.value
                                    );
                                  }}
                                  required
                                  containerClass="mb-0"
                                />
                              </MDBCol>
                            </MDBRow>
                            <MDBRow className="pt-3">
                              <MDBCol>
                                <MDBInput
                                  label="From"
                                  group
                                  type="date"
                                  name="DateFrom"
                                  value={o.DateFrom}
                                  onChange={(e) => {
                                    validate();
                                    handleChange(
                                      index,
                                      e.target.name,
                                      e.target.value
                                    );
                                  }}
                                  required
                                  containerClass="mb-0"
                                />
                              </MDBCol>
                              <MDBCol>
                                <MDBInput
                                  label="To"
                                  group
                                  type="date"
                                  name="DateTo"
                                  value={o.DateTo}
                                  onChange={(e) => {
                                    validate();
                                    handleChange(
                                      index,
                                      e.target.name,
                                      e.target.value
                                    );
                                  }}
                                  required
                                  containerClass="mb-0"
                                />
                              </MDBCol>
                            </MDBRow>
                            <MDBRow>
                              <MDBCol>
                                <MDBInput
                                  label="Nature of Organization"
                                  group
                                  type="text"
                                  name="natureOfOrganization"
                                  value={o.natureOfOrganization}
                                  onChange={(e) => {
                                    validate();
                                    handleChange(
                                      index,
                                      e.target.name,
                                      e.target.value
                                    );
                                  }}
                                  required
                                  containerClass="mb-0"
                                />
                              </MDBCol>
                            </MDBRow>
                          </div>
                        </MDBCard>
                      ))}
                    </MDBRow>

                    <MDBRow
                      center
                      className="d-flex  align-items-center mt-3 mb-3"
                    >
                      <MDBCol className="d-flex justify-content-end">
                        <MDBBtn onClick={addOrganization}>+</MDBBtn>
                      </MDBCol>
                      <MDBCol className="d-flex justify-content-start">
                        <MDBBtn onClick={removeOrganization}>-</MDBBtn>
                      </MDBCol>
                    </MDBRow>

                    <MDBRow
                      center
                      className="d-flex flex-column align-items-center"
                    >
                      {touched && errorMsg.length > 0 && (
                        <div className="text-danger mb-2">{errorMsg}</div>
                      )}
                      {touched && !errorMsg && (
                        <Link to={nextStep}>
                          <MDBBtn
                            color="pink"
                            rounded
                            type="button"
                            className="z-depth-1a"
                          >
                            Next
                          </MDBBtn>
                        </Link>
                      )}
                      {(!touched || errorMsg) && (
                        <MDBBtn
                          color={"grey"}
                          disabled
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      )}
                    </MDBRow>
                  </>
                )}
              </form>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
