import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step11(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please enter valid email address" : "");
  };

 
  return (
    <Step heading={`Do you have a current email address?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
       
          <MDBRow center className="mt-5">
            <MDBCol sm={10} md={8}>
              <MDBRow center>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "hasEmail", true);
                    }}
                  >
                    Yes
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "hasEmail", false);
                      onChangeField(e, "email", null);
                      history.push(nextStep);
                    }}
                  >
                    No
                  </MDBBtn>
                </MDBCol>
              </MDBRow>

              {state.hasEmail && (
                <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                  <MDBRow center>
                    <MDBCol md={6}>
                      <MDBInput
                        label="Email"
                        group
                        type="email"
                        name="email"
                        value={state.email}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    {touched && errorMsg.length > 0 && (
                      <div className="text-danger mb-2">{errorMsg}</div>
                    )}
                    {touched && !errorMsg && (
                      <Link to={nextStep}>
                        <MDBBtn
                          color="pink"
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      </Link>
                    )}
                    {(!touched || errorMsg) && (
                      <MDBBtn
                        color={"grey"}
                        disabled
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBRow>
                </form>
              )}
            </MDBCol>
          </MDBRow>

      </MDBContainer>
    </Step>
  );
}
