// add conditionals --shubs

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBFormInline,
  MDBCard,
} from "mdbreact";
import Step from "./Step";

export default function Step19(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const [allegianceToUS, setAllegianceToUS] = useState({
    allegianceToUS1: "",
    allegianceToUS2:"",
  });

  const Questions = [
    {
      question:
        "Do you believe in the U.S. government and the Constitution, and are you willing to take the oath of allegiance to the U.S.?",
      name: "allegianceToUS1",
    },

    {
      question:
        "If required, would you be willing to bear arms, perform noncombatant services, and other civilian work on behalf of the U.S. ",
      name: "allegianceToUS2",
    },
  ];

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please enter valid phone number" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  const handleChange = (name, value) => {
    const values = allegianceToUS;
    console.log(values[1]);
    values[name] = value;
    setAllegianceToUS(values);
    onChangeField("e", "allegianceToUS", allegianceToUS);
  };

  return (
    <Step heading={" Other Factors "}>
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              {Questions.map((q, index) => (
                <MDBCard md={12} className="p-4 mt-3">
                  <MDBCol md={12}>
                    <div>
                      <h6
                        style={{
                          width: "100%",
                          fontSize: "larger",
                          fontWeight: "bold",
                        }}
                      >
                        {index + 1}
                        {". "}
                        {q.question}
                      </h6>
                    </div>
                  </MDBCol>
                  <MDBCol>
                    <MDBFormInline className="justify-content-center">
                      <MDBInput
                        gap
                        onClick={() => handleChange(q.name, true)}
                        checked={allegianceToUS[q.name] === true ? true : false}
                        label="Yes"
                        type="radio"
                        id="radio1"
                        style={{ display: "flex" }}
                        containerClass="mr-5"
                      />
                      <MDBInput
                        gap
                        onClick={() => handleChange(q.name, false)}
                        checked={allegianceToUS[q.name] === false ? true : false}
                        label="No"
                        type="radio"
                        id="radio2"
                        style={{ display: "flex" }}
                        containerClass="mr-5"
                      />
                    </MDBFormInline>
                  </MDBCol>
                </MDBCard>
              ))}

          
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
