//Spouse socialSecurityNumber--Number , alienNumber --boolean, what is your spouses date of place of naturalization?

import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {countries} from "./countries"
export default function Step9(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const form2Ref = new React.createRef();

  const validate = () => {

    
    setTouched(true);
    if(form2Ref.current !== null){
    const isValid = formRef.current.checkValidity() && form2Ref.current.checkValidity();
    if (!form2Ref.current.checkValidity()) {
      setErrorMsg("Please enter a valid Alien Number");
    } else {
          setErrorMsg(!isValid ? "Please enter  valid details" : "");
    }}
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  return (
    <Step heading={`Please enter Spouse details`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBCol>
                <MDBInput
                  color="pink"
                  label="Social Security Number"
                  group
                  pattern="\d[\d,]*"
                  type="text"
                  name="SpouseSocialSecurityNumber"
                  value={state.SpouseSocialSecurityNumber}
                  onChange={(e) => {
                    validate();
                    onChangeField(e, e.target.name, e.target.value);
                  }}
                  required
                  containerClass="mb-0"
                />
              </MDBCol>
              <div className="my-4" />
              <MDBRow className="mt-3 mb-3">
                <MDBCol sm={10} md={6}>
                  <label className="mt-3 mb-3">Place of Naturalization</label>
                  <select
                    className="browser-default custom-select"
                    name="placeOfNaturalization"
                    value={state.placeOfNaturalization}
                    onChange={(e) => {
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                  >
                    <option selected disabled>
                      Choose your option
                    </option>
                    {countries.map((country) => (
                      <option value={country.name}>{country.name}</option>
                    ))}
                  </select>
                </MDBCol>
                <MDBCol sm={10} md={6}>
                  <label className="mt-3 mb-3">Date of Naturalization</label>
                  <MDBInput
                    group
                    type="date"
                    name="dateOfNaturalization"
                    value={state.dateOfNaturalization}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0 mt-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md={12}>
                  <label className="mt-3 mb-3">
                    Spouse Current Immigration Status if not US Citizen
                  </label>
                  <MDBInput
                    color="pink"
                    label="Immigration Status"
                    group
                    type="text"
                    name="spouseImmigrationStatus"
                    value={state.spouseImmigrationStatus}
                    pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                    onChange={(event) =>
                      onChangeField(
                        "index",
                        event.target.name,
                        event.target.value
                      )
                    }
                    required
                    containerClass="mb-0 mt-0"
                  />
                </MDBCol>
              </MDBRow>
              <MDBRow center className="mt-5">
                <h4>Does your Spouse have Alien Number</h4>
              </MDBRow>
              <MDBRow center>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "spousehasAlien", true);
                    }}
                  >
                    Yes
                  </MDBBtn>
                </MDBCol>
                <MDBCol className="d-block d-lg-none my-2"></MDBCol>
                <MDBCol md={6} lg={4}>
                  <MDBBtn
                    color="pink"
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "spousehasAlien", false);
                      onChangeField(e, "spouseAlienNumber", null);
                      history.push(nextStep);
                    }}
                  >
                    No
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
              {state.spousehasAlien && (
                <form onSubmit={(e) => e.preventDefault()} ref={form2Ref}>
                  <MDBRow center>
                    <MDBCol md={6}>
                      <MDBInput
                        label="Alien Number"
                        group
                        min="1000000"
                        max="999999999"
                        type="number"
                        name="spouseAlienNumber"
                        value={state.spouseAlienNumber}
                        onChange={(e) => {
                          validate();
                          onChangeField(e, e.target.name, e.target.value);
                        }}
                        required
                        containerClass="mb-0 mt-5"
                      />
                    </MDBCol>
                  </MDBRow>
                  <MDBRow
                    center
                    className="d-flex flex-column align-items-center"
                  >
                    {touched && errorMsg.length > 0 && (
                      <div className="text-danger mb-2">{errorMsg}</div>
                    )}
                    {touched && !errorMsg && (
                      <Link to={nextStep}>
                        <MDBBtn
                          color="pink"
                          rounded
                          type="button"
                          className="z-depth-1a"
                        >
                          Next
                        </MDBBtn>
                      </Link>
                    )}
                    {(!touched || errorMsg) && (
                      <MDBBtn
                        color={"grey"}
                        disabled
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    )}
                  </MDBRow>
                </form>
              )}
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}

const normalizeInput = (str) => {
  let number = str.replace(/,/g, "").replace(/\$/g, "");
  let result = parseFloat(number);
  return isNaN(result) ? str : `$${result.toLocaleString()}`;
};
