import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";


export default function Step2(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [inputRef, setInputRef] = useState(new React.createRef());

  const validate = () => {
    setTouched(true);
    const isValid = inputRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out proper values" : "");
  };
  useEffect(() => {
    validate();
  });
  return (
    <Step heading={"What is the name used on your Alien registration card?"}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={inputRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <MDBRow>
                <MDBCol md={12}>
                  <MDBInput
                    color="pink"
                    label="Name as of Alien registration card"
                    group
                    pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                    type="text"
                    name="alienRegistrationCard"
                    value={state.alienRegistrationCard}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>

              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
