import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step16(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();
    setErrorMsg(!isValid ? "Please fill out proper values" : "");
  };

  return (
    <Step heading={`Do you use any other additional names?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "additionalNames", true);
                  }}
                >
                  Yes
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "additionalNames", false);
                    onChangeField(e, "additionalName", null);
                    history.push(nextStep);
                  }}
                >
                  No
                </MDBBtn>
              </MDBCol>
            </MDBRow>
            {state.additionalNames && (
              <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
                <MDBRow>
                  <MDBCol md={12}>
                    <MDBInput
                      color="pink"
                      label="Additional Full Name"
                      group
                      pattern="^[a-zA-Z ][a-zA-Z\\s ]+$"
                      type="text"
                      name="additionalName"
                      value={state.additionalName}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      required
                      containerClass="mb-0 mt-5"
                    />
                  </MDBCol>
                </MDBRow>
                <MDBRow
                  center
                  className="d-flex flex-column align-items-center"
                >
                  {touched && errorMsg.length > 0 && (
                    <div className="text-danger mb-2">{errorMsg}</div>
                  )}
                  {touched && !errorMsg && (
                    <Link to={nextStep}>
                      <MDBBtn
                        color="pink"
                        rounded
                        type="button"
                        className="z-depth-1a"
                      >
                        Next
                      </MDBBtn>
                    </Link>
                  )}
                  {(!touched || errorMsg) && (
                    <MDBBtn
                      color={"grey"}
                      disabled
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  )}
                </MDBRow>
              </form>
            )}
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
